import React from "react";
import "./NavPopup.css";
import Header from "../Header/Header";

function NavPopup(props) {
  return(
    <div className="nav-popup">
      <Header handleNavState={props.handleNavState}></Header>
      <div className="nav-popup__links">
        <a className="nav-popup__link" href="#about" onClick={props.handleNavState}>About</a>
        <a className="nav-popup__link" href="#service" onClick={props.handleNavState}>Service</a>
        <a className="nav-popup__link" href="#project" onClick={props.handleNavState}>Project</a>
        <a className="nav-popup__link" href="#portfolio" onClick={props.handleNavState}>Portfolio</a>
        <a className="nav-popup__link" href="#blog" onClick={props.handleNavState}>Blog</a>
        <a className="nav-popup__link" href="#contact-us" onClick={props.handleNavState}>Contact Us</a>
      </div>
      <button className="nav-popup__btn" type="button">Sign in</button>
    </div>
  )
}

export default NavPopup;