import React from "react";
import makeCarousel from 'react-reveal/makeCarousel';
import Slide from 'react-reveal/Slide';
import "./Main.css";
import photo1 from '../../images/photo-1586023492125-27b2c045efd7.jfif';
import photo2 from '../../images/photo-1571898231547-11cd8a41ed84.jfif';
import photo3 from '../../images/photo-1564320382348-c06ae02a3897.jfif';
import photo4 from '../../images/photo-1609103110787-d4ad0e40687f.jfif';
import photo5 from '../../images/photo-1614461059234-a26df6cb9ab8.jfif';
import photo6 from '../../images/photo-1618760918333-972c632d697c.jfif';
import photo7 from '../../images/photo-1619992677751-cb736bd47e2e.jfif';
import photo8 from '../../images/photo-1609103110697-518155dfe5b8.jfif';
import photo9 from '../../images/photo-1618219908412-a29a1bb7b86e.jfif';
import photo10 from '../../images/photo-1616611213095-58abb651f70c.jfif';

function Main() {

  const CarouselUI = ({ position, handleClick, children }) => (
    <div className="main__carousel">
      {children}
      <button type="button" onClick={handleClick} data-position={position - 1} className="main__left-btn">&lt;</button>
      <button type="button" right onClick={handleClick} data-position={position + 1} className="main__right-btn">&gt;</button>
    </div>
  );

  const Carousel = makeCarousel(CarouselUI);

  return(
    <div className="main">
      <div className="main__background">
      </div>
      <div className="main__container">
        <h1 className="main__title">WE MAKE YOUR</h1>
        <h2 className="main__title"><span>DREAM HOUSE</span></h2>
        <p className="main__subtitle">We Have largest Team to create your Dream Building. We are AlwyesWanted to Your Trusted Business Partner</p>
        <a className="main__contact-btn" href="#contact-us">CONTACT NOW</a>
      </div>
      <Carousel>
        <Slide right>
          <img className="main__img" src={photo1} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo2} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo3} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo4} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo5} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo6} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo7} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo8} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo9} alt="фото домов"></img>
        </Slide>
        <Slide right>
          <img className="main__img" src={photo10} alt="фото домов"></img>
        </Slide>
      </Carousel>
    </div>
  )
}

export default Main;