import React from "react";
import "./Portfolio.css";
import photo1 from "../../images/photo-1523217582562-09d0def993a6.jfif";
import photo2 from "../../images/photo-1617978241112-898785df45b9.jfif";
import photo3 from "../../images/photo-1562438668-bcf0ca6578f0.jfif";
import photo4 from "../../images/photo-1616137466211-f939a420be84.jfif";
import photo5 from "../../images/photo-1618221195710-dd6b41faaea6.jfif";
import photo6 from "../../images/photo-15643203823418-c06ae02a3897.jfif";
import photo7 from "../../images/photo-1616047006789-b7af5afb8c20.jfif";
import photo8 from "../../images/photo-1617326021886-53d6be1d7154.jfif";
import photo9 from "../../images/photo-1616486338812-3dadae4b4ace.jfif";
import photo10 from "../../images/photo-1614255976202-8ce52bfcb655.jfif";
import Fade from 'react-reveal/Fade';

function Portfolio() {
  const imgList = [photo1, photo2, photo3, photo4, photo5, photo6, photo7, photo8, photo9, photo10];
  
  const [imgIndex, setImgIndex] = React.useState(1);
  const rightImgIndex = `${imgIndex === 9 ? 0 : imgIndex + 1}`;
  const leftImgIndex = `${imgIndex === 0 ? 9 : imgIndex - 1}`;

  function handleRightImg() {
    if (imgIndex === 9) {
      setImgIndex(0)
    } else {
      setImgIndex(imgIndex + 1);
    }
  }

  function handleLeftImg() {
    if (imgIndex === 0) {
      setImgIndex(9)
    } else {
      setImgIndex(imgIndex - 1);
    }
  }

  return(
    <Fade bottom>
      <div className="portfolio" id="portfolio">
        <h3 className="portfolio__title">Portfolio</h3>
        <div className="portfolio__line"></div>
        <p className="portfolio__subtitle">Chake out some of Our Portfolio.</p>
        <div className="portfolio__gallery">
          <img alt="project" src={imgList[leftImgIndex]} onClick={handleLeftImg} className="portfolio__photo1"></img>
          <img alt="project" src={imgList[imgIndex]} className="portfolio__photo2"></img>
          <img alt="project" src={imgList[rightImgIndex]} onClick={handleRightImg} className="portfolio__photo3"></img>
        </div>
      </div>
    </Fade>
  )
}

export default Portfolio;